export default {
	data() {
		return {
			form:{
				userCode:'',	   //目标用户id
				usde:'',			//金额
				payPassword:'',	//支付密码

			},
			ratio:'',
			userDetail:''
		};
	},
	onLoad:function(){
		this.fetchRatio()
		this.fetchUserDetail()
		console.log(this.$store.userCode)
	},
	methods:{
		async fetchUserDetail(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
		},
		quzheng:function(){
			this.form.usde = parseInt(this.form.usde/10)*10
		},
		allin:function(){
			this.form.usde = this.userDetail.signinUsde
		},
		async fetchRatio(){
			const res = await this.$http.get('/deal/signin_shift_ratio')
			this.ratio = res.data.data.msg
		},
		async submit(){
			if (this.form.usde<10){
				uni.showToast({
					title: '數量最少10',
					icon: 'none',
					duration: 1000
				});
				return
			}
			if(this.form.userCode == this.$store.userCode){
				uni.showToast({
					title: '不能轉賬給自己',
					icon: 'none',
					duration: 1000
				});
				return
			}
			this.form.usde = parseInt(this.form.usde/10)*10
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/deal/signin_shift',{data: this.form})
			uni.hideLoading()
			
			uni.showToast({
				title: '轉賬成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: 1
				});
			}, 2000);

		},
		scanCodeToken:function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        this.form.userCode = res.result
			    }
			});
		}
	}
}
